header {
    padding: 10px;
    display: flex;
    align-items: center;

    gap: 5%;
    margin-right: max(7%, 50px);
    margin-left: 50px;

    height: 12vh;
}

.header__cart-open {
    width: calc(97% - var(--cart-width));
}

.header__h1 {
    font-size: 4rem;
}

.header__ShopDiv {
    margin-left: auto;
}

.header__p {
    font-size: 3rem;
}


.header__button {
    color: var(--letter);
    background-color: transparent;
    border: none;
    padding: 5px 10px;
}

    .header__button:hover {
        cursor: pointer;

        outline: 1px solid var(--letter);
        border-radius: 3px;
    }

    .header__button:active {
        cursor: pointer;
        color: var(--letter2);
        outline: 1px solid var(--letter2);
    }

/* Mobile */
@media (max-width: 767px) {
    header {
        height: 5vh;
    }

    .header__h1 {
        font-size: 1rem;
    }

    .header__p {
        font-size: 1rem;
    }

    .header__cart-open {
        width: 0%;
    }
}

/* Tablet */
@media (min-width: 768px) and (max-width: 1024px) {
    header {
        height: 5vh;
    }

    .header__h1 {
        font-size: 1.9rem;
    }

    .header__p {
        font-size: 1.5rem;
    }

    .header__cart-open {
        width: calc(93% - var(--cart-width));
    }
}

/* Mobile wide*/
@media (max-height: 767px) {
    header {
        height: 12vh;
    }

    .header__h1 {
        font-size: 1rem;
    }

    .header__p {
        font-size: 1rem;
    }

    .header__cart-open {
        width: 0%;
    }
}